import listIcon from '../../images/services/service/listicon.svg';
export default function Component({ data, img, lists }) {
    return (
        <div className='service-content-container' style={{ boxShadow: 'rgba(0, 0, 0, 0.25) 0px 0px 4px 0px', borderRadius: "8px", padding: '2rem' }}>
            <h2>{data.title}</h2>
            <p className='sub-txt'>{data.text}</p>
            <div className='service-content-wrapper'>
                <div style={{ display: 'flex', flexFlow: 'column', rowGap: '2rem' }}>
                    {
                        lists.map((item, index) => {
                            const width = window.innerWidth;
                            if (width <= 600) {
                                return (
                                    <div key={index} style={{ display: 'flex', flexFlow: 'column', rowGap: '1rem' }}>
                                        <div style={{ display: 'flex', columnGap: '12px', alignItems: 'center' }}>
                                            <img src={listIcon} alt="" />
                                            <h3>{item.title}</h3>
                                        </div>
                                        <div>
                                            <p className='sub-txt-2'>{item.text}</p>
                                        </div>
                                    </div>
                                )
                            } else {
                                return (
                                    <div key={index} style={{ display: 'flex', columnGap: '4rem' }}>
                                        <div>
                                            <img src={listIcon} alt="" />
                                        </div>
                                        <div>
                                            <h3>{item.title}</h3>
                                            <p className='sub-txt-2'>{item.text}</p>
                                        </div>
                                    </div>
                                )
                            }
                        }
                        )
                    }
                </div>
                <div>
                    <img className='img' src={img} alt="" />
                </div>
            </div>
        </div>
    )
}