import React from 'react';
import { Link } from "react-router-dom";
import SubsPopup from './SubsPopup';
import { PopupButton } from "react-calendly";

export default class Contact extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
          name: '',
          email:'',
          phone:'',
          company:'',
          desYourIdea:'',
          formErrors: {name:'',email: '',phone:'',company:'',Idea:''},
          nameValid: false,
          emailValid: false,
          phoneValid: false,
          companyValid: false,
          desYourIdeaValid: false,
          formValid: false
          };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.validateField = this.validateField.bind(this);
        this.validateForm = this.validateForm.bind(this);
      }
   
    handleChange(e) { 
      const name = e.target.name;
      const value = e.target.value;
      this.setState({[name]: value},() => { this.validateField(name, value) });
     }
     validateField(fieldName, value) {
      let fieldValidationErrors = this.state.formErrors;
      let nameValid = this.state.nameValid;
      let emailValid = this.state.emailValid;
      let phoneValid = this.state.phoneValid;
      let companyValid = this.state.companyValid;
      let desYourIdeaValid = this.state.desYourIdeaValid;
    
      switch(fieldName) {
        case 'name':
          nameValid = value.length >= 1;
          fieldValidationErrors.name = nameValid ? '': 'This field is required';
          break;
        case 'email':
          emailValid = value.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i);
          fieldValidationErrors.email = emailValid ? '' : ' is invalid';
          break;
        case 'phone':
          phoneValid = value.length >= 1;
          fieldValidationErrors.phone = phoneValid ? '': 'This field is required';
          break;
        case 'company':
            companyValid = value.length >= 1;
            fieldValidationErrors.company = companyValid ? '': 'This field is required';
            break;
        case 'desYourIdea':
          desYourIdeaValid = value.length >= 1;
          fieldValidationErrors.Idea = desYourIdeaValid ? '': 'This field is required';
          break;
        default:
          break;
      }
      this.setState({formErrors: fieldValidationErrors,
                      nameValid: nameValid,
                      emailValid: emailValid,
                      phoneValid: phoneValid,
                      companyValid: companyValid,
                      desYourIdeaValid: desYourIdeaValid,
                    }, this.validateForm);
    }
    validateForm() {
      this.setState({formValid: this.state.nameValid && this.state.emailValid && this.state.phoneValid  && this.state.companyValid && this.state.desYourIdeaValid});
    }
  handleSubmit(event) {
    document.getElementById('subs-txt-container').style.display='block';
    document.getElementById('subs-popup').style.display='block';
    document.getElementById('loader').style.display='block';
    document.getElementById('contact-txt').style.display='none';
    document.getElementById('newsletter-txt').style.display='none';
    event.preventDefault();
    fetch('https://thayakam-api.herokuapp.com/store', {
        method: 'POST',
        body: JSON.stringify(this.state),
        headers: {
          'Content-type': 'application/json; charset=UTF-8',
        },
        
      })
         .then(response => response.json())
         .then((data) => {
              document.getElementById('subs-txt-container').style.display='block';
              document.getElementById('loader').style.display='none';
              document.getElementById('contact-txt').style.display='block';
              document.getElementById('newsletter-txt').style.display='none';

         
         })
         .catch((err) => {
        
         });
      
  }
    render(){
    return(
      <div id='contact-container'>
         <h1 id='contact-title'>Reach Out and Say Hello! 👋</h1>
        <div id='contact-msg-box-container' style={{display:'flex',columnGap:'3rem'}}>
          <div className='contact-msg-box'>
            <h3 className='contact-msg-title-txt sm'>Kickstart a Consultation 🌟</h3>
            <p>
              Kickstart your ideas ✨ with the guidance of
              our experts 👩‍💼👨‍💼, as we collaborate to breathe
              life into your vision 🌱</p>
              <div>
              <PopupButton
                        url="https://calendly.com/thaayakamltd?primary_color=15ceff"
                        rootElement={document.getElementById("root")}
                        text="Schedule a consultation 📆"
                        className="schedule-btn"
                      />
              </div>
          </div>
          <div className='contact-msg-box'>
          <h3 className='contact-msg-title-txt sm'>Message Us on WhatsApp 📱</h3>
            <p>
            Reach out to us effortlessly through WhatsApp.
            📲 Drop us a message and let's explore
            how we can help you succeed. 🚀
            </p>
              <div>
                <a className="contact-btn-explore" href='https://wa.me/message/DWHRSHNA7ZJKC1' target="_blank">Reach via WhatsApp 💬</a>
              </div>
          </div>
        </div>
        <div className="contact-form-main">
          <h2>Initiate Contact with Form 📇</h2>
          <p id='contact-form-subtitle'>Initiate contact by filling out the form below 📇. We're excited to explore possibilities together 🚀.</p>
          <div id="subs-popup" style={{display:'none'}}><SubsPopup /></div>
            <div className="contact-form-container">
                <form className="contact-form" onSubmit={this.handleSubmit}>
                    <div><input type="text" id="name" name="name" placeholder="Name" value={this.state.name} onChange={this.handleChange} /></div>
                    <p>{this.state.formErrors.name}</p>
                    <div><input type="email" name="email" id="email" placeholder="Email" value={this.state.email} onChange={this.handleChange} /></div>
                    <p>{this.state.formErrors.email}</p>
                    <div><input type="tel" pattern="[0-9]{3}-[0-9]{2}-[0-9]{3}" id="phone" name="phone" placeholder="Phone/WhatsApp" value={this.state.phone} onChange={this.handleChange} /></div>
                    <p>{this.state.formErrors.phone}</p>
                    <div><input type="text" id="company" name="company" placeholder="Company" value={this.state.company} onChange={this.handleChange} /></div>
                    <p>{this.state.formErrors.company}</p>
                    <div><textarea  id="des-your-idea" name="desYourIdea" placeholder="Describe your Idea" value={this.state.desYourIdea} onChange={this.handleChange} ></textarea></div>
                    <p>{this.state.formErrors.Idea}</p>
                    <div className="contact-form-btn-container">
                        <div><input disabled={!this.state.formValid} type="submit" value="Submit 📨"/></div>
                    </div>
                </form> 
    
            </div>
   
        </div>
        <div>
          <h2>Send us an Email 📧</h2>
        <div id='contact-msg-box-container' style={{display:'flex',columnGap:'3rem'}}>
          <div className='contact-msg-box'>
            <h4 className='sm'>General Inquiry</h4>
            <p>
            Reach out to us directly via email ✉️.
            We're ready to engage in a productive
            conversation about your requirements 💼.
            </p>
              <div>
                <Link className="contact-btn-explore" to="we-are">hello@thaayakam.co.uk</Link>
              </div>
          </div>
          <div className='contact-msg-box'>
          <h4 className='sm'>Careers Inquiry</h4>
            <p>
            Engage with us through email to discuss
            potential career opportunities ✉️.
            Let's explore your future with us. 🌟
            </p>
              <div>
                <Link className="contact-btn-explore" to="we-are">careers@thaayakam.co.uk</Link>
              </div>
          </div>
        </div>
        </div>
      </div>
    );
    }
}